@charset "UTF-8";
.nav {
  display: flex;
  align-items: center;
  position: relative;
}

.list {
  display: flex;
  gap: 24px;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.item {
  margin-right: 20px;
}

.link {
  text-decoration: none;
  color: white;
  font-size: 1rem;
}

.link:hover {
  background-color: grey;
}

/* Бургер скрыт на больших экранах */
.burger {
  display: none;
  width: 30px;
  height: 30px;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}

.burger span {
  display: block;
  height: 3px;
  width: 100%;
  background: whitesmoke;
  transition: 0.3s;
}

/* Мобильное меню */
.mobileMenu {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50px;
  right: 0;
  gap: 10px;
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  z-index: 10;
  list-style: none;
}

/* Показываем бургер на маленьких экранах */
@media (max-width: 768px) {
  .link {
    color: black;
    font-size: 1.5rem;
  }
  .list {
    display: none;
  }
  .burger {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
}
@media (min-width: 769px) {
  .active {
    background: purple;
    display: inline-block;
    min-height: 100px;
    display: flex;
    align-items: center;
    padding: 10px;
  }
}/*# sourceMappingURL=headerNav.module.css.map */